@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
   scroll-behavior: auto;
  }

  *,
  *::before,
  *::after {
    -webkit-animation-duration: 0.01ms !important;
            animation-duration: 0.01ms !important;
    -webkit-animation-iteration-count: 1 !important;
            animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}



.new-recipe__controls {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
  color: #40005d;
}

.new-recipe__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new-recipe__control input,
textarea {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new-recipe__control div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

h4 {
  padding: 0 0 1rem 0;
  color: #fff;
  text-align: left;
}

.new-recipe__actions {
  text-align: right;
}

.new-recipe__controls .new-recipe__control button {
  margin-left: 10px;
  width: 42px;
  height: 42px;
  padding: 0px;
}

.new-recipe {
  background-color: #a892ee;
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-recipe button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #40005d;
  background-color: #40005d;
  color: white;
  border-radius: 12px;
}

.new-recipe button:hover,
.new-recipe button:active {
  background-color: #510674;
  border-color: #510674;
}

.new-recipe button.alternative {
  color: #220131;
  border-color: transparent;
  background-color: transparent;
}

.new-recipe button.alternative:hover,
.new-recipe button.alternative:active {
  background-color: #ddb3f8;
}

.card {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.modal header {
  min-height: 60px;
  height: 60px;
  color: #ffffff;
  background-color: #800000;
  border-radius: 12px;
  display: flex;
  justify-content: space-between;
  padding-left: 14px;
}

.modal header h4 {
  margin: 0;
  text-align: left;
}

.modal header span {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  font-size: 20px;
  opacity: 0.8;
  cursor: pointer;
}

.modal header span:hover {
  opacity: 1;
}

.hide {
  display: none;
}

.show {
  display: block;
}

.recipe {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: #4b4b4b;
  color: #fff;
}

.recipe__directions {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: flex-start;
  flex: 1 1;
}

.recipe__directions__row {
  display: flex;
  justify-content: space-between;
}

.recipe h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1 1;
  margin: 0 1rem;
  color: white;
}

.recipe__directions div > div {
  margin: 0 1rem 1rem;
  color: white;
  background-color: #585050;
  border: 1px solid black;
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.recipe__directions img {
  width: 200px;
  height: 200px;
  border-radius: 12px;
  border: 1px solid black;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  margin: 0 1rem;
}

.recipe__directions button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #40005d;
  background-color: #40005d;
  color: white;
  border-radius: 12px;
}

.recipe__directions button:hover,
.recipe__directions button:active {
  background-color: #510674;
  border-color: #510674;
}

.new-recipe button.alternative {
  color: #220131;
  border-color: transparent;
  background-color: transparent;
}

.recipe__directions button.alternative:hover,
.recipe__directions button.alternative:active {
  background-color: #ddb3f8;
}

.recipe__directions .modal {
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 2000;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.75);
  padding-top: 2%;
}

.recipe__directions .modal div {
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  width: 95vw;
  max-width: 600px;
  height: 90vh;
  max-height: 700px;
  position: relative;
  overflow: hidden;
  border-radius: 12px;
  margin: 0 auto;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
}

.recipes {
  padding: 1rem;
  background-color: rgb(31, 31, 31);
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
}

.recipes ul {
  list-style: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

.recipes p {
  color: white;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #8a2b06;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.main-image {
  width: 100%;
  height: 25rem;
  z-index: 0;
  overflow: hidden;
}

.main-image img {
  width: 120%;
  height: 100%;
  object-fit: cover;
  -webkit-transform: rotateZ(-5deg) translateY(-4rem) translateX(1rem);
          transform: rotateZ(-5deg) translateY(-4rem) translateX(1rem);
}

